export default {
    data() {
        return {
            assertResult: '',
            resultFlag: null,
            inputFlag: false,
            resultList: [],
            inputList: [],
        }
    },
    mounted() {
        this.gameNextLevel();
    },
    methods: {
        gameRefreshLevel() {
            this.inputList = this.buildInputList();
            this.resultList = this.buildResultList();
        },
        gameNextLevel() {
            this.assertResult = this.buildAssertResult();
            this.gameRefreshLevel();
        },
        gameCheckResult() {
            const assertResult = this.resultList.map(e => e.value).join('');
            this.resultFlag = (assertResult === this.assertResult);
            this.inputFlag = false;
        }
    },
    watch: {
        resultList: {
            handler(resultList) {
                const assertResult = resultList.map(e => e.value).join('');
                this.inputFlag = assertResult.length === this.assertResult.length;
                this.resultFlag = null;
                // console.log('this.inputFlag', this.inputFlag);
                // if (assertResult.length === this.assertResult.length) {
                //     this.resultFlag = (assertResult === this.assertResult);
                // } else {
                //     this.resultFlag = null;
                // }
            },
            deep: true
        }
    }
}