export default {
    methods: {
        tileListFindById(tileList, id) {
            return tileList.find(e => e.id === id);
        },
        tileListFindIdle(tileList) {
            return tileList.find((e) => {
                return e.relationId === null && e.value === null;
            });
        },
        tileListRemoveGap(tileList, gapId, tileListRelation) {
            for (let i = gapId; i < tileList.length; i++) {
                if (i === tileList.length-1 || tileList[i+1].relationId === null) {
                    tileList[i].relationId = null;
                    tileList[i].value = null;
                    tileList[i].class.used = false;
                    break;
                }
                tileList[i].relationId = tileList[i+1].relationId;
                tileList[i].value = tileList[i+1].value;
                tileList[i].class.used = tileList[i+1].class.used;
                
                //fix relation
                if (tileListRelation && tileListRelation[tileList[i].relationId]) {
                    tileListRelation[tileList[i].relationId].relationId = i; 
                }   
            }
        },
    }
}