export default {
    methods: {
        tileLetterCreate(id, value, relationId = null) {
            return {
                id: id,
                value: value,
                relationId: relationId,
                class: {
                    filled: (value !== null && relationId === null),
                    disabled: (relationId !== null),
                }
            }
        },
        tileBind(tile, id, value = null)  {
            if (tile) {
                if (typeof id === 'number') {
                    tile.relationId = id;
                    tile.class.disabled = true;
                }
                if (value !== null) {
                    tile.value = value;
                }
            }
        },
        tileUnbind(tile, isFlushValue) {
            if (tile) {
                tile.relationId = null;
                tile.class.disabled = false;
                if (isFlushValue === true) {
                    tile.value = null;
                }
            }
        },
        tileBindByTile(tile, byTile, withFeedback) {
            if (tile && byTile) {
                this.tileBind(tile, byTile.id, byTile.value);
                if (withFeedback === true) {
                    this.tileBind(byTile, tile.id);
                }
            }
        },
        tileIsUsed(tile) {
            return tile ? tile.relationId !== null : undefined;
        },
        tileCreate(id, value) {
            return {
                id: id,
                value: value,
                class: {
                    disabled: false
                }
            }
        },
    }
}