<template>
    <div class="controls-panel">
        <div class="controls-group left">
            <div 
                @click="clickBackHandler"
                class="control tile-blue svg-back">
            </div>
        </div>
        <div class="controls-group center">
            <div
                v-if="typeof refreshFlag === 'boolean'" 
                class="control tile-blue svg-reload"
                :class="{disabled: !refreshFlag}"
                @click="clickControl('clickRefresh', refreshFlag)"
            ></div>
            <div
                v-if="typeof hintFlag === 'boolean'" 
                class="control tile-blue svg-lamp"
                :class="{disabled: !hintFlag}"
                @click="clickControl('clickHint', hintFlag)"
            ></div>
        </div>
        <div class="controls-group right">
            <div 
                v-if="typeof checkFlag === 'boolean'"
                class="control tile-blue svg-check"
                :class="{disabled: !checkFlag}"
                @click="clickControl('clickCheck', checkFlag)"
            ></div>
            <div 
                v-if="typeof nextFlag === 'boolean'"
                class="control tile-blue svg-play"
                :class="{disabled: !nextFlag}"
                @click="clickControl('clickNext', nextFlag)"
            ></div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['refreshFlag', 'checkFlag', 'nextFlag', 'hintFlag'],
    methods: {
        clickBackHandler() {
            this.$router.push({ name: 'home'});
        },
        clickControl(emit, flag = true) {
            if (flag) {
                this.$emit(emit)
            }
        }
    }
}
</script>

