<template>
    <div class="tile-item">
        <span
            v-for="(valueItem, i) in valueList"
            :key="i"
            class="tile-content"
        >
            {{valueItem}}
        </span>
    </div>
</template>

<script>
    export default {
        props: ['value'],
        computed: {
            valueList() {
                return Array.from('' + (this.value ?? ''))
            }
        },
    }
</script>

<style scoped>
    .tile-item.disabled {
        opacity: .7;
    }

    .tile-item.disabled .tile-content {
        opacity: .5;
    }
    .tile-content {
        animation: fadein 0.3s;
    }

    @keyframes fadein {
        from { opacity: 0; }
        to { opacity: 1; }
    }
</style>