import Dictionary from '../config/dictionary';

export default {
    data() {
        return {
            dictionaryListId: [],
        }
    },
    methods: {
        dictionaryGetAlphabet() {
            return 'абвгґдеєжзиіїйклмнопрстуфхцчшщьюя';
        },
        dictionaryGetWordRandom() {
            return Dictionary[Math.floor(Math.random() * Dictionary.length)].word;
        },
        dictionaryGetWordByList() {
            if (this.dictionaryListId.length === 0) {
                this.dictionaryBildListId();
            }
            return Dictionary[this.dictionaryListId.pop()].word;
        },
        dictionaryBildListId() {
            this.dictionaryListId = [...Dictionary.keys()].sort(() => 0.5 - Math.random());
        },
    }
}